.switch {
	width: 63.6%;
	position: relative;
	input {
		position: absolute;
		top: 0;
		z-index: 2;
		opacity: 0;
		cursor: pointer;
		&:checked {
			z-index: 1;
			& + label {
				opacity: 1;
				cursor: default;
			}
		}
		&:not(:checked) {
			& + label {
				&:hover {
					opacity: 0.5;
				}
			}
		}
	}
	label {
		opacity: 0.33;
		transition: opacity 0.25s ease;
		cursor: pointer;
	}
	.toggle-outside {
		height: 100%;
		border-radius: 2rem;
		padding: 3px;
		overflow: hidden;
		transition: 0.25s ease all;
	}
	.toggle-inside {
		border-radius: 5rem;
		background: #858585;
		position: absolute;
		transition: 0.25s ease all;
	}
}
.switch--horizontal {
	display: flex;
	align-items: center;
	height: 31px;

	input {
		height: 30px;
		width: 140px;
		left: 0;
		margin: 0;
		&:checked {
			& ~ .toggle-outside {
				.toggle-inside {
					left: 3px;
				}
			}
		}
		& ~ input {
			&:checked {
				& ~ .toggle-outside {
					.toggle-inside {
						left: 32px;
					}
				}
			}
		}
	}
	input#radio-b{
		left: 140px
	}
	label {
		display: inline-block;
		width: 6rem;
		// height: 100%;
		margin: 0;
		&:last-of-type {
			margin-left: 6rem;
		}
	}
	.toggle-outside {
		background: #fff;
		position: absolute;
		width: 60px;
		left: 110px;
	}
	.toggle-inside {
		height: 25px;
		width: 25px;
	}
}


.checkbox-text {
	display: flex;
	position: relative;
	padding-left: 25px;
	margin-bottom: 12px;
	cursor: pointer;
	transition: 0.25s ease color;
	user-select: none;
}
.checkbox-text  {
	.text{
		line-height: 10px;
		font-weight: 500;
		font-size: 11px;
		color: #767676;
	}
	input {
		position: absolute;
		opacity: 0;
		cursor: pointer;
		height: 0;
		width: 0;
		&:checked {
			~ {
				.checkmark {
					background-color: #858585;
					&:after {
						display: block;
					}
				}
			}
		}
	}
	&:hover {
		input {
			~ {
				.checkmark {
					background-color: #858585;
				}
			}
		}
	}
	.checkmark {
		&:after {
			left: 5px;
			top: 2px;
			width: 5px;
			height: 8px;
			border: solid white;
			border-width: 0 1.5px 1.5px 0;
			-webkit-transform: rotate(45deg);
			-ms-transform: rotate(45deg);
			transform: rotate(45deg);
		}
	}
}
.checkmark {
	position: absolute;
	top: 2px;
	left: 0;
	height: 15px;
	width: 15px;
	transition: 0.25s ease all;
	background-color: #fff;
	&:after {
		content: "";
		position: absolute;
		display: none;
	}
}

