		}
	}
}

.about-us-intro-section{
	padding-bottom: 65px;
	position: relative;

	.about-info{
		padding: 40px 0;
	

		.text-info{
			display: flex;
   			flex-direction: column;
    		justify-content: center;

			.text{
				font-weight: 400;
				font-size: 23px;
				text-align: left;
				color: #000000;


				margin-bottom: 40px;

				&:last-child{
					margin-bottom: 0;
				}
			}
		}
		.img{
			position: relative;
			height: 395px;
			img{
				position: absolute;
				top: 0;
				left: 0;
				width: 100%;
				height: 100%;
				object-fit: cover;
				object-position: center center;
			}
		}
		&::after{
			position: absolute;
			content: "";
			top: 0;
			right: 0;
			width: 30%;
			height: 480px;
			background: #eaeaea;
			z-index: -1;
			transform: translateY(110px);
		}
	}
}

